import './App.css'
import { useState } from 'react'
import { toBeInTheDocument } from '@testing-library/jest-dom/dist/matchers'

function App() {
  let post = '경은우동맛집'
  //스테이트
  let [title, titleChange] = useState([
    '우동 추천',
    '남자 코트 추천',
    '파이썬 독학',
  ])
  let [logo, setLogo] = useState('ReactBlog')
  let [good, goodChange] = useState([0, 0, 0])
  let [input, setInput] = useState('')
  let [modal, setModal] = useState(false)
  let [titleState, titleStateChange] = useState(0)

  return (
    <div className="App">
      <div className="blog">
        <h4 style={{ color: 'red', fontSize: '16px' }}>블로그</h4>
      </div>
      {title.map(function (a, i) {
        return (
          <div className="list" key={i}>
            <h4
              onClick={() => {
                setModal(true)
                titleStateChange(i)
              }}
            >
              {title[i]}
              <span
                onClick={(e) => {
                  e.stopPropagation()
                  let copyGood = [...good]
                  copyGood[i] = copyGood[i] + 1
                  goodChange(copyGood)
                }}
              >
                👍
              </span>{' '}
              {good[i]}{' '}
              <button
                onClick={(e) => {
                  e.stopPropagation()
                  let copy = [...title],
                    copyGood = [...good]
                  copy.splice(i, 1)
                  copyGood.splice(i, 1)
                  titleChange(copy)
                  goodChange(copyGood)
                }}
              >
                삭제
              </button>
            </h4>
            <p>11월 7일</p>
          </div>
        )
      })}
      <input
        onChange={(e) => {
          //유저 입력값 받기
          setInput(e.target.value)
        }}
      ></input>
      <button
        onClick={() => {
          titleChange([input, ...title])
          goodChange([0, ...good])
          //titleChange(title.concat(input)) 뒤에 추가
        }}
      >
        등록
      </button>
      {modal == true ? (
        <Modal
          color={'skyblue'}
          titleState={titleState}
          title={title}
          titleChange={titleChange}
        />
      ) : null}
    </div>
  )
}

//컴포넌트 방식1, 프롭스
const Modal = (props) => {
  return (
    //prop로 함수 패러미터 같은 기능 수행
    <div className="modal" style={{ background: props.color }}>
      <h4>{props.title[props.titleState]}</h4>
      <p>날짜</p>
      <p>상세내용</p>
      <button
        onClick={() => {
          let copyTitle = [...props.title]
          copyTitle[0] = '여자 코트 추천'
          props.titleChange(copyTitle)
        }}
      >
        제목 수정 버튼
      </button>
    </div>
  )
}
//컴포넌트 방식2
function Component() {
  return (
    <div className="Component">
      <h4>제목</h4>
      <p>날짜</p>
      <p>상세내용</p>
    </div>
  )
}

export default App
